.islandora img {
	max-width: 100%;
	*width: 100%;
}

.islandora * {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

/*
 * These rules will display DTs/DDs as columns.
 * Constructs must follow a key/value pair pattern.
 * The three last declarations are meant to kill white space between DTs/DDs
 * (result of inline-block styling)
 */

dl.islandora-inline-metadata {
	margin: 0 auto;
	letter-spacing: -0.31em;
	*letter-spacing: normal;
	word-spacing: -0.43em;
}

.islandora-inline-metadata dt {
	margin-right: -1px;
	width: 20%;
	font-family: $roboto_condensed;
	/*font-weight: bold;*/
	padding-right: 0;
	word-wrap: normal;
}

.islandora-inline-metadata dd {
	width: 80%;
	padding-left: 40px;
}

dl.islandora-metadata-fields {
	width: 100%;
}

.islandora-metadata dt,
.islandora-metadata dd {
	border-top: 1px solid #e5e5e5;
}

.islandora-metadata dt.first,
.islandora-metadata dd.first {
	border-top: 0;
}

/*
 * In this rule, we reset the white-space (see hack above)
 */
.islandora-inline-metadata dt,
.islandora-inline-metadata dd {
	display: inline-block;
			 *display: inline;
			 zoom: 1;
			 letter-spacing: normal;
			 word-spacing: normal;
			 vertical-align: top;
			 padding-top: 6px;
			 padding-bottom: 4px;
			 margin: 0;
		 }

		 /*
		  *scholar-page
		  */

.scholar-page {
	legend {
		display: none;
	}
	fieldset.islandora-metadata	{
		border: 0px;
		padding:0;
		background-color:none;
		background:none;
		margin-bottom:0px;
	}
	p {
	margin: 0;
}
}
