//------------------------
//-  lp citations block  -
//------------------------
.block--recent-citations--title {
	font-size: 1em;
	font-family: $roboto;
	font-weight: normal;

	@include clean;
}

.block--recent-citations--publication {
	font-style: italic;
}

.block--recent-citations dd {
	@include icon(f105);

	margin-bottom: $base-spacing / 2;
}

.block--recent-citations--more {
	@extend input[type="button"];

	margin-top: $base-spacing / 2;
}

//=======================

.csl-bib-body {
	font-family: $georgia;
	padding-right: $base-spacing / 2;
}

.citation-page {
	.csl-bib-body {
		font-size: 1.2em;
		background: $light-green;
		margin-bottom: $base-spacing;
		padding: $base-spacing / 2;
		margin-right: 0;
		border: $base-border;

		//@include small-shadow;

	}
}

fieldset#edit-bookmarks-fieldset td a {
	font-family: $roboto_condensed;
	font-weight: 600;
}

/*
 *style select list
 */

#islandora-scholar-citation-select-form {
	display: inline-block;
}

#islandora-scholar-citation-select-form label {
	display: none;
}

#islandora-scholar-citation-select-form .description {
	font-size: .9em;
	font-family: $roboto_condensed;
	/*text-align: center;*/
	text-transform: uppercase;
}

.citation-page #block-islandora-bookmark-islandora-bookmark {
	/* width: 164px;*/
	/*float: right;*/
}

.citation-page h1 {
	font-size: 1.6em;
}

.citation-page {
	.islandora-inline-metadata dt {
		width: 12%;
	}

	.islandora-inline-metadata dd {
		width: 88%;
	}
}

//------------------------------------------
//-  thesis thumbnail and download button  -
//------------------------------------------

.ir_thesisCModel > a > img {
	border: $base-border;
	width: 200px;
}

//I am not proud of this, could break other stuff
.ir_thesisCModel {
	.form-item {
		label {
			display: none;
		}

		a {
			@include icon(f019);

			@extend input[type="submit"];

			@extend button.green;

			width: 200px;
			text-align: center;
		}
	}
}

.citation-page {
	form#islandora-scholar-citation-select-form {
		@include span-columns(3);
	}

	.block--islandora-solr-search-navigation {
		@include span-columns(6);
		.block--search-navigation--list {
			display: flex;
			justify-content: center;

		}
	}

	#block-islandora-bookmark-islandora-bookmark {
		@include span-columns(3);
		@include omega;
		#islandora-bookmark {
			float:right;
			width: 140px;
		}
	}
	.ir_thesisCModel.fedora_system_FedoraObject_3_0,
	.ir_citationCModel.fedora_system_FedoraObject_3_0 {
	    clear: both;
	}
}
